.card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 30px;
  position: relative;
  width: 200px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card > .img-container {
  overflow: hidden;
  margin: 10px
}

.card > .img-container-selected {
  overflow: hidden;
  margin:0;
}

.card > .img-container > img {
  height: 100%;
  width: 100%;
}
