.card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 30px;
  position: relative;
  width: 200px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card > .img-container {
  overflow: hidden;
  margin: 10px
}

.card > .img-container-selected {
  overflow: hidden;
  margin:0;
}

.card > .img-container > img {
  height: 100%;
  width: 100%;
}

.wrapper {
  background-image: url(/images/background.jpg);
  background-size: cover;
  margin:0 auto;

  width: 1200px;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%
}

